@mixin clearfix {
  &::after {
    content: "";
    display: table;
    clear: both;
  }
}


@mixin position-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

// MEDIA QUERY MANAGER
/*
0 - 600px:   Phone
600-900px:   Tablet portrait
900-1200px:   Tablet landscape
1800px +:   Big desktop

$breakpoint argument choices:
-  phone
-  tab-port
-  tab-land
-  big-desktop

ORDER: Base + typograpfy > general layout + grid > page layout > components

1em = 16px
*/

//mobile first: min-width

@mixin respond($breakpoint) {
  @if $breakpoint == phone-land {
    @media only screen and (min-width: 37.5em) { @content };   //600px
  }
  @if $breakpoint == tab-port {
    @media only screen and (min-width: 48em) { @content };    //768px
  }
  // @if $breakpoint == tab-port {
  //   @media only screen and (min-width: 56.25em) { @content };    //900px
  // }
  @if $breakpoint == tab-land {
    @media only screen and (min-width: 75em) { @content };   //1200px
  }
  @if $breakpoint == big-desktop {
    @media only screen and (min-width: 112.5em) { @content };  //1800px
  }
}



